/* Large desktop */
.header-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  z-index: 100;
}

.header-background {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
}

.main-content {
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  text-align: center;
}
@media (max-width: 480px) {
  .main-content {
    padding: 1rem;
  }
}

.modalite-container {
  height: 5rem;
  width: 80%;
  display: flex;
  flex-direction: column;
  margin: 2rem;
  transition: 2s all;
  border-radius: 3rem;
}
@media (max-width: 480px) {
  .modalite-container {
    height: 40rem;
    background: white;
  }
}
@media (min-width: 480px) and (max-width: 1099px) {
  .modalite-container {
    height: 35rem;
    background: white;
  }
}
.modalite-container:hover {
  height: 30rem;
  background: white;
}
.modalite-container:hover > .span-modalite {
  display: flex;
}

h3 {
  font-family: var(--ff-title);
  font-weight: 200;
  font-size: 4rem;
  color: var(--bordeaux);
  cursor: pointer;
  margin-top: 1rem;
}

.span-modalite {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s all;
  color: transparent;
}
@media (max-width: 480px) {
  .span-modalite {
    display: flex;
    color: var(--bordeaux);
  }
}
@media (min-width: 480px) and (max-width: 1099px) {
  .span-modalite {
    display: flex;
    color: var(--bordeaux);
  }
}

.style-text-osteo {
  margin: 1rem;
  font-size: 2rem;
}

.style-text-osteo-active {
  color: var(--bordeaux);
}

.osteo-text-container {
  margin: 2rem;
  width: 80%;
}

.osteo-text {
  font-size: 2rem;
